import React from 'react';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import twelveType from '../../api/twelveType';
import Divider from '../child/Divider';
import { updateUser, storeResult, signOut, resetAnswers } from '../../actions/index';
import { Card, Icon, Image, Grid, Segment, Progress } from 'semantic-ui-react';
import Chart from 'react-apexcharts'

class DashboardPage extends React.Component {
  constructor() {
    super()
    this.state = {
      optionsBar: {
        chart: {
          stacked: true,
          stackType: '100%',
          toolbar: {
            show: false
          }
        },
        plotOptions: {
          bar: {
            horizontal: true,
          },
        },
        dataLabels: {
          dropShadow: {
            enabled: true
          }
        },
        stroke: {
          width: 0,
        },
        xaxis: {
          categories: [''],
          type: 'Top Archetype',
          labels: {
            show: false
          },
          axisBorder: {
            show: false
          },
          axisTicks: {
            show: false
          }
        },
        fill: {
          opacity: 1,
          type: 'gradient',
          gradient: {
            shade: 'dark',
            type: "vertical",
            shadeIntensity: 0.35,
            gradientToColors: undefined,
            inverseColors: false,
            opacityFrom: 0.85,
            opacityTo: 0.85,
            stops: [90, 0, 100]
          },
        },

        legend: {
          position: 'bottom',
          horizontalAlign: 'right',
        }
      },
      seriesBar: [{
        name: 'blue',
        data: [100]
      }, {
        name: 'green',
        data: [0]
      }, {
        name: 'yellow',
        data: [0]
      }],
      optionsPie: {
        labels: ['A']
      },
      seriesPie: [100],
      chart: false
    }
  }

  componentDidMount = async () => {
    window.scrollTo(0, 0)

    if(this.props.isSignedIn === false) {
      this.props.history.push('/login')
    } else if (this.props.answers.length > 0) {
      try {
        const response = await twelveType.get('/user/quizResult', {
          headers: {
            Authorization: 'Bearer ' + this.props.token
          },
          params:  {
            answers: this.props.answers }
        })
        this.props.updateUser(response.data.user.name, response.data.user.archetype, response.data.user.created_at, this.props.token)
        this.props.storeResult(response.data.percentage)

        // pie update data
        const newPieLabels = Object.keys(response.data.top_answers)
        const newPieSeries = []
        newPieLabels.map((data) => {
          newPieSeries.push(response.data.top_answers[data])
          return true
        })

        const newBarSeries = Object.keys(response.data.top_answers).reduce((carry, key) => {
          return [
            ...carry,
            {
              name: key,
              data: [response.data.top_answers[key]]
            }
          ]
        }, []);

        this.setState({
          seriesBar: newBarSeries, //update chart
          seriesPie: newPieSeries,
          optionsPie: {
            labels: newPieLabels
          },
          chart: true
        })

      } catch {
        console.log('error bro');
      }
    } else if (this.props.archetype.length > 0 && this.props.answers.length === 0) {
      try {
        const response = await twelveType.get('/user/quizResult', {
          headers: {
            Authorization: 'Bearer ' + this.props.token
          },
          params:  {
            answers: this.props.archetype }
        })
        this.props.updateUser(response.data.user.name, response.data.user.archetype, response.data.user.created_at, this.props.token)
        this.props.storeResult(response.data.percentage)

        // pie update data
        const newPieLabels = Object.keys(response.data.top_answers)
        const newPieSeries = []
        newPieLabels.map((data) => {
          newPieSeries.push(response.data.top_answers[data])
          return true
        })

        const newBarSeries = Object.keys(response.data.top_answers).reduce((carry, key) => {
          return [
            ...carry,
            {
              name: key,
              data: [response.data.top_answers[key]]
            }
          ]
        }, []);

        this.setState({
          seriesBar: newBarSeries, //update chart
          seriesPie: newPieSeries,
          optionsPie: {
            labels: newPieLabels
          },
          chart: true
        })

      } catch {
        console.log('error bro');
      }
    }
  }

  // onRetakeQuiz = () => {
  //   this.props.updateUser(this.props.username, "", this.props.token)
  //   this.props.history.push('/quiz')
  // }
  onSignOut = () => {
    this.props.updateUser('','','','')
    this.props.resetAnswers()
    this.props.signOut()
    this.props.history.push('/')
  }

  render() {
    return (
      <div className="ui vertical stripe quote segment">
        <Divider title={'Hi, '+ this.props.username} />
        <div className="ui container">
        <Grid>
          <Grid.Column width={5}>
            <Card>
            <Image src='https://react.semantic-ui.com/images/avatar/large/matthew.png' wrapped ui={false} />
            <Card.Content>
              <Card.Header>{this.props.username}</Card.Header>
              <Card.Meta>
                <span className='date'>Joined in { this.props.register.substr(0,4)}</span>
              </Card.Meta>
              <Card.Description>
                {this.props.username} is a {this.props.result.length > 0 ? Object.keys(this.props.result[0]) : 'You should take quiz first to describe your archetype'}.
              </Card.Description>
            </Card.Content>
            <Card.Content extra>
              <span>
                <Icon name='circle' className="green-icon" />
                Online
              </span>
              <span onClick={() => this.onSignOut()} className="logout-text">
                <Icon name='sign in alternate' className="red-icon" />
                Logout
              </span>
            </Card.Content>
          </Card>
        </Grid.Column>
        <Grid.Column width={11}>
          {this.state.chart ?
            <Chart
              options={this.state.optionsBar} height={140}
              series={this.state.seriesBar}
              type="bar"
              width={700}
            />
            // <Segment>
            //   <Progress percent={75} inverted progress success>
            //     success
            //   </Progress>
            //   <Progress percent={15} inverted progress warning>
            //     warning
            //   </Progress>
            //   <Progress percent={10} inverted progress error>
            //     error
            //   </Progress>
            // </Segment>
          : <p>is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.
            <Link className="b-primary ui huge button" to="/quiz">
                TAKE THE QUIZ
              </Link>
          </p> }
          {this.state.chart ?
          <Chart
            options={this.state.optionsPie}
            series={this.state.seriesPie}
            type="donut"
            width="380"
          />
          : '' }
        </Grid.Column>
      </Grid>
        </div>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    isSignedIn: state.auth.isSignedIn,
    archetype: state.auth.archetype,
    username: state.auth.username,
    register: state.auth.register,
    token: state.auth.token,
    answers: state.quiz.answers,
    result: state.quiz.result
  }
}

export default connect(
  mapStateToProps, {
    updateUser, storeResult, signOut, resetAnswers
  }
)(DashboardPage);